<template>
    <div class="login_app">
        <div class="rtePwd">
            <h3>修改密码</h3>
            <div class="rtePwd_ipt">
                <span>用户名：</span> 
                <van-cell-group>
                    <van-field v-model="uname"  placeholder="请输入用户名" ref="id" />
                </van-cell-group>
            </div>
            <div class="rtePwd_ipt">
                <span>新密码：</span> 
                <van-cell-group>
                    <van-field v-model="upwd"  placeholder="请输入新密码"/>
                </van-cell-group>
            </div>
            <van-button type="info" class="rtePwd_btn" @click="onRetrievePwd">
                修改密码
            </van-button>
            <div>
                <a href="javascript:;" @click="toLogin">返回登录</a>
            </div>
        </div>
    </div>  
</template>
<script>
import '../css/login_register_retrievePwd.css'
export default {
    data(){
        return{
            uname:'',//用户名称
            upwd:''//用户密码
        }
    },
    mounted(){
        this.$refs.id.focus();
    },
    methods:{
        toLogin(){
            this.$router.push('/gwc/Login')
        },
        onRetrievePwd(){
            var obj1={uname:this.uname};
            var obj2={uname:this.uname,upwd:this.upwd};
            console.log(obj1)
            this.$axios.get("/api/select_user",{params:obj1}).then(res=>{
                if(res.data.code==1){
                    this.$axios.get("/changePwd",{params:obj2}).then(res=>{
                        if(res.data.code==1){
                            this.$toast("密码修改成功");
                        }else{
                            this.$toast("密码修改失败");
                        }
                    })
                }else{
                    this.$toast("用户不存在！");
                    return;
                }
            }).catch(err=>{
                console.log(err);
            })
        }
    }
}
</script>